import { LitElement, html, css } from "lit";
import { customElement, property } from "lit/decorators.js";

export interface ChordDataColor {
  [label: string]: string
}

export interface ChordLabelOptions {
  show: boolean;
  color: string;
  fontSize: number
}

@customElement("setect-chord")
export class Chord extends LitElement {
  static styles = [
    css`
      :host {
        display: block;
      }
    `,
  ];

  // Data
  @property() matrixData: number[][] = undefined 
  @property() sourceLabels: string[] = []
  @property() targetLabel: string[] = []

  // Styles
  @property() colors: ChordDataColor = {}
  @property({ type: Number}) tickUnit: number = 5
  @property({type: Boolean}) hideTick: boolean = false
  @property() labelOptions: ChordLabelOptions = {
    show: true,
    color: 'rgb(119, 119, 119)',
    fontSize: 9
  }


  render() {
    return html`Hello vinh Nga`;
  }
}
